
.header-contenedor{
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
    background: #e2e2e2 ;
    opacity: 0.9;
    padding-top: 1rem;
    box-shadow: 0px -1px 27px -7px rgba(31,31,31,0.75);
    -webkit-box-shadow: 0px -1px 27px -7px rgba(31,31,31,0.75);
    -moz-box-shadow: 0px -1px 27px -7px rgba(31,31,31,0.75);
    padding-bottom:30px;
}

.contenedor-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid var(--black) 2px;
    width: fit-content;
    margin-top: 1rem;
    margin:0%;
}

.logo{
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 600;
    margin: 0.5rem 0.5rem; 
    color: var(--black);
    text-decoration: none;
}



.redes{
    display: flex;
    gap: 2rem;
    color: var(--black);
}

.redes-active{
    display: flex;
    gap: 2rem;
    color: var(--black);
}

.red{
    font-size: 2rem;
}

.red a{
    color: var(--black);
}

.red a:hover{
    color: var(--red);
    transition: 0.2s;
}

.nav-icon{
    display:none;
    background:none;
    border:none;
    font-size:40px;
    width:0px;
}

.navbar-container_visible {
    left: 0;
  }

@media(max-width: 768px) {
    .header-contenedor{
        display: flex;
        justify-content:space-around;
        align-items:center;
        width: 100%;
        height:70px;
        gap: 1rem;
        padding: 20px 0px;
    }

    .redes{
       display:none;
    }

    .redes-active{
        display:flex;
        position:absolute;
        z-index:3;
        top:547px;
    }

    .red{
        margin-top: 2rem ;
    }



    .logo{
        font-weight: bold;
    }

    .nav-icon{
        display:block;
        font-size:20px;
    }

    .navbar-container{
        background-color:var(--red);
        position: fixed;
        top: 100px;
        width: 100%;
        padding: 20px 0;

        height: calc(100% - 60px);
        overflow-y: auto;
    
        left:200%;
        transition: left 0.3s;
    }

    .navbar-container-active{
        background-color:var(--red);
        position: fixed;
        top: 100px;
        width: 100%;
        padding: 20px 0;

        height: calc(100% - 60px);
        overflow-y: auto;
    
        left:0%;
        transition: left 0.3s;
    }
}